.img-picker-container {
    grid-area: 1 / 18 / 21 / 20;
    min-height: 220px;
    max-height: 600px;
    overflow-y: scroll;
    overflow-x:hidden;
    width: 55px;
}

.img-picker-container img {
    height: 40px;
    width: 40px;
}



